<script>


import Vue from 'vue'
import subHeader from '@/views/part/subheader.vue'
Vue.component("sub-header",subHeader)


import settings from '@/views/part/settings.vue'
import header from '@/views/part/header.vue'
import navigation from '@/views/part/navigation.vue'
// import auth from './part/auth'

export default {
   components:{
    'part-settings': settings,
    'part-header': header,
    'part-navigation': navigation,
    // 'part-auth': auth
  },
  created(){
    if(!this.$store.state.auth){
      this.$router.push("/login")
    }
  },
  data(){
      return{
          showMenu: true,
          activeClass: 'text-center text-xs w-full rounded-lg mt-1  bg-theme_primary_dark text-primary',
          nonActiveClass: 'text-center text-xs w-full  text-primary mt-1',
          activeClassLg: 'text-left text-lg p-1 px-8 mx-4 rounded-full bg-theme_primary_dark text-primary',
          nonActiveClassLg: 'text-left text-lg p-1 px-8 mx-4 rounded-full text-primary hover:bg-theme_primary_dark',
      }
  },
  computed:{
    settings(){
      return this.$store.state.leftMenu +" "+ this.$store.state.theme+" "+ this.$store.state.primaryColor;
    }
  }

}
</script>

<style  scoped>
.text-xs{
  font-size:8px
}
.slide-fade-enter-active {
  transition: all .2s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to{
  opacity: .5;
}
</style>